.item{
    width: 280px;
    padding-left: 50px 50px;
    margin-left: 50px;
}
.item img{
    width: 280px;
}
.item p{
    margin: 6px 0px ;
}
.item-price{
    display: flex;
    gap:20px;
}
.item-price-new{
    color:#374151;
    font-size:18px;
    font-weight: 600;
}
.item-price-old{
    color: #8c8c8c;
    text-decoration: line-through;
    font-size: 18px;
    font-weight:500;
}
.item :hover{
    transform: scale(1.05);
    transition: 0.6s;
}